import React, {Component} from 'react';
import styles from './Hello.module.scss';
import {DOMAIN} from '../../../configs/config';
import BgImage from '../BgImage/BgImage';

class Hello extends Component {
  render() {
    return (
      <div id={styles.Hello}>
        <BgImage bgImage={require('../../../images/home/3903262.jpg')}>
          <div className={styles.icon}>
            <img width='200' src={require('../../../images/home/icon/1.png')} alt=""/>
            <img width='100' src={require('../../../images/home/icon/48f66faa890744baf2b0901a056b9932.png')} alt=""/>
            <img width='150' src={require('../../../images/home/icon/images.png')} alt=""/>
            <img width='200' src={require('../../../images/home/icon/unnamed.png')} alt=""/>
          </div>
        </BgImage>

        <div className={styles.body}>
          {/*主体*/}
          <div className={styles.text}>
            <h1 className="animated bounceInDown">2020年即使在困难</h1>
            <h2 className="animated bounceInDown delay-1s">也不要停止向前</h2>
          </div>
          {/*跳转链接*/}
          <div className={styles.button + ' animated rollIn delay-2s'}>
            <span className={'iconfont icon-shouzhi animated shake slow infinite ' + styles.finger}></span>
            <a href={`${DOMAIN}/mylog/index`}>
              <button className="myButton">我的个人主页</button>
            </a>
          </div>
        </div>
      </div>
    );
  }
}

export default Hello;
