import React, {Component} from 'react';
import PropTypes from 'prop-types';
import styles from './BgImage.module.scss';

class BgImage extends Component {
  constructor(props) {
    super(props);
  }

  static propTypes = {
    bgImage: PropTypes.string,
    mask: PropTypes.bool
  };

  static defaultProps = {
    mask: true,
  };

  render() {
    const {bgImage, mask} = this.props;
    return (
      <div>
        {/*背景图*/}
        <div style={{backgroundImage: `url(${bgImage})`}}
             className={styles.backGround}/>
        {/*背景遮罩*/}
        {mask ? <div className={styles.mask}/> : ''}
        {this.props.children}
      </div>
    );
  }


}

export default BgImage;
